import React, { useState, useContext } from 'react';
import * as firebase from 'firebase/app';
import 'firebase/firestore';
import { LoggedInContext } from '../context';
import OnboardingIntro from './OnboardingIntro';
import OnboardingBedtime from './OnboardingBedtime';
import OnboardingAddTask from './OnboardingAddTask';
import '../styles/Onboarding.scss';

export default function Onboarding() {
  const { userSettings } = useContext(LoggedInContext);
  const [step, setStep] = useState(0);

  const setOnboardingComplete = () => {
    firebase.firestore().collection('userSettings').doc(userSettings.user).update({ onboarded: true });
  };

  return (
    <div className="onboarding">
      {step === 0 && <OnboardingIntro setStep={setStep} />}
      {(step === 1 || step === 2) && <OnboardingAddTask step={step} setStep={setStep} userSettings={userSettings} />}
      {step === 3 && <OnboardingBedtime userSettings={userSettings} nextStep={setOnboardingComplete} />}
    </div>
  );
}
