import React from 'react';
import useToggle from '../hooks/useToggle';
import HabitLimits from './HabitLimits';
import '../styles/Header.scss';
import logo from '../assets/images/logo.svg';
import settings from '../assets/images/settings.svg';
import downDark from '../assets/images/downDark.svg';
import { useContext } from 'react';
import { UserContext } from '../context';

const Header = () => {
  const { handleSignOut } = useContext(UserContext);
  const [dropdown, toggleDropdown, setDropdown] = useToggle(false);
  const hideDropdown = () => setDropdown(false);
  if (window.location.hash === '#popup') return null;
  return (
    <header className="header" onMouseLeave={hideDropdown}>
      <img src={logo} alt="Confetti Logo" />
      <HabitLimits dark hasPopup />
      <div className="header__menu">
        <button type="button" className="header__menuButton" onClick={toggleDropdown}>
          <img src={settings} alt="Settings Icon" />
          <img src={downDark} alt="Menu indicator" />
        </button>
        {dropdown && (
          <div className="header__dropdown">
            <a className="header__dropdownItem" href="mailto:hello@tryconfetti.io">
              Email us
            </a>
            <a className="header__dropdownItem" href="https://www.tryconfetti.io">
              Go to tryconfetti.io
            </a>
            <button type="button" className="header__dropdownItem" onClick={handleSignOut}>
              Sign Out
            </button>
          </div>
        )}
      </div>
    </header>
  );
};

export default Header;
