/* global chrome */
import React, { ChangeEvent, FormEvent, useState } from 'react';
import * as firebase from 'firebase/app';
import 'firebase/auth';

import { useToggle } from '../hooks';
import logo from '../assets/images/logo-white.svg';
import googleIcon from '../assets/images/googleIcon.svg';
import facebookIcon from '../assets/images/facebookIcon.svg';
import '../styles/Login.scss';

const Login = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isSignUp, toggleSignUp] = useToggle(false);
  const [error, setError] = useState('');

  const handleChangeName = (e: ChangeEvent<HTMLInputElement>) => setName(e.target.value);
  const handleChangeEmail = (e: ChangeEvent<HTMLInputElement>) => setEmail(e.target.value);
  const handleChangePassword = (e: ChangeEvent<HTMLInputElement>) => setPassword(e.target.value);
  const handleError = (err: firebase.FirebaseError) => {
    if (err.code === 'auth/user-not-found') return setError('User not found!');
    if (err.code === 'auth/wrong-password') return setError('Wrong Password!');
    return setError(err.message);
  };

  const handleEmailLogin = (e: FormEvent) => {
    e.preventDefault();
    firebase
      .auth()
      .signInWithEmailAndPassword(email, password)
      .catch((err) => handleError(err));
  };
  const handleEmailSignUp = async (e: FormEvent) => {
    e.preventDefault();
    try {
      const response = await firebase.auth().createUserWithEmailAndPassword(email, password);
      response.user!.updateProfile({ displayName: name });
    } catch (e) {
      handleError(e);
    }
  };
  const handleGoogleLogin = () => {
    // @ts-ignore
    if (chrome.identity) {
      // @ts-ignore
      chrome.identity.getAuthToken({ interactive: true }, (token: string) => {
        const credential = firebase.auth.GoogleAuthProvider.credential(null, token);
        firebase
          .auth()
          .signInWithCredential(credential)
          .then(async (response) => {
            // @ts-ignore
            const responseName = response.additionalUserInfo?.profile?.name;
            const isNewUser = response.additionalUserInfo?.isNewUser;
            if (isNewUser) await response.user?.updateProfile({ displayName: responseName });
          })
          .catch((err) => {
            handleError(err);
          });
      });
    } else {
      const provider = new firebase.auth.GoogleAuthProvider();
      provider.addScope('email');
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(async (response) => {
          // @ts-ignore
          const responseName = response.additionalUserInfo?.profile?.name;
          const isNewUser = response.additionalUserInfo?.isNewUser;
          if (isNewUser) await response.user?.updateProfile({ displayName: responseName });
        })
        .catch((err) => {
          handleError(err);
        });
    }
  };
  const handleFacebookLogin = () => {
    const provider = new firebase.auth.FacebookAuthProvider();
    provider.addScope('email');
    firebase.auth().useDeviceLanguage();
    firebase
      .auth()
      .signInWithPopup(provider)
      .then(async (response) => {
        // @ts-ignore
        const responseName = response.additionalUserInfo?.profile?.name;
        const isNewUser = response.additionalUserInfo?.isNewUser;
        if (isNewUser) await response.user?.updateProfile({ displayName: responseName });
      })
      .catch((err) => {
        handleError(err);
      });
  };

  return (
    <div className="login">
      <img className="login__logo" src={logo} alt="Confetti Logo" />
      <div className="login__container">
        <form onSubmit={isSignUp ? handleEmailSignUp : handleEmailLogin}>
          <p className="login__error">{error}</p>
          {isSignUp && <input type="text" value={name} onChange={handleChangeName} placeholder="Name" />}
          <input type="email" value={email} onChange={handleChangeEmail} placeholder="Email" />
          <input type="password" value={password} onChange={handleChangePassword} placeholder="Password" />
          <button type="submit" data-type="Email">
            {isSignUp ? 'Sign Up with Email' : 'Login with Email'}
          </button>
        </form>
        <div className="login__separator">
          <small>OR</small>
          <hr />
        </div>
        <button type="button" onClick={handleGoogleLogin} data-type="Google">
          <img src={googleIcon} alt="Google Icon" />
          {isSignUp ? 'Sign Up with Google' : 'Login with Google'}
        </button>
        <button type="button" onClick={handleFacebookLogin} data-type="Facebook">
          <img src={facebookIcon} alt="Facebook Icon" />
          {isSignUp ? 'Sign Up with Facebook' : 'Login with Facebook'}
        </button>
        {isSignUp ? (
          <button type="button" data-type="Text" onClick={toggleSignUp}>
            Already have an account? <span>Login</span>{' '}
          </button>
        ) : (
          <button type="button" data-type="Text" onClick={toggleSignUp}>
            Don't have an account? <span>Sign Up</span>{' '}
          </button>
        )}
      </div>
    </div>
  );
};

export default Login;
