import React, { useContext, useEffect, useState } from 'react';

import { LoggedInContext } from '../context';
import apiClient from '../services/apiClient';
import backgroundConfetti from '../assets/images/backgroundConfetti.svg';
import exitIcon from '../assets/images/exit.svg';
import '../styles/PremiumModal.scss';

const PremiumModal = () => {
  const { togglePremiumModal } = useContext(LoggedInContext);
  const [prices, setPrices] = useState({ month: 250, year: 2500 });

  useEffect(() => {
    const fetchPrices = async () => {
      const { data } = await apiClient.getPrices();
      setPrices(data);
    };
    fetchPrices();
  }, []);

  return (
    <div className="premiumModal">
      <img className="premiumModal__background" src={backgroundConfetti} alt="Confetti Background" />
      <img className="premiumModal__exitIcon" src={exitIcon} alt="Exit" onClick={togglePremiumModal} />
      <div className="premiumModal__container">
        <h1>Upgrade to Track Unlimited Habits</h1>
        <h2>You have used up your 3 free habits.</h2>
        <div className="premiumModal__button -blue">
          <div className="premiumModal__buttonTop">Billed Yearly</div>
          <div className="premiumModal__buttonBottom">
            <h3>${(prices.year / 100).toFixed(2)}</h3>
            <h4>per year</h4>
            <a href="https://www.tryconfetti.io?upgrade=true">Upgrade</a>
          </div>
        </div>
        <div className="premiumModal__button">
          <div className="premiumModal__buttonTop">Billed Monthly</div>
          <div className="premiumModal__buttonBottom">
            <h3>${(prices.month / 100).toFixed(2)}</h3>
            <h4>per month</h4>
            <a href="https://www.tryconfetti.io?upgrade=true">Upgrade</a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PremiumModal;
