import { Dispatch, useState } from 'react';

const useToggle = (defaultState: boolean) => {
  const [isActive, setIsActive] = useState<boolean>(defaultState);
  const toggleIsActive = () => setIsActive((prev) => !prev);
  return [isActive, toggleIsActive, setIsActive] as [boolean, () => void, Dispatch<boolean>];
};

export default useToggle;
